.contacts{
    font-weight: 500; 
    font-size: 11px;
    line-height: 12px;
    display: flex;
    .contacts-item {
        padding-right: 24px;
        display: flex;
        align-items: center;
        text-decoration: none;
        color: #ffffff;
    
        &:last-child {
          padding-right: 0;
        }
    
        .contacts-item-img {
          padding-right: 8px;
        }
      }
}

@media all and (max-width: 660px) {
    .contacts {
      font-size: 8px;
      line-height: 10px;
    }
  }
  
  @media all and (max-width: 540px) {
    .contacts {
      flex-direction: column;
    }
  }